@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Rajdhani:wght@500&family=Special+Elite&display=swap);
nav,.head,h1,h2{font-family:'Special Elite', cursive}main{font-family:'Rajdhani', sans-serif}*{margin:0}body{background-color:#0b0c10;background:#083431;background:linear-gradient(90deg, #083431 0%, #04534e 57%, #0b0c10 94%);font-size:20px;line-height:1.5rem;min-height:calc( 100vh + 1px);behavior:smooth}p,ul,li{color:#c5c6c7}p:first-letter,ul:first-letter,li:first-letter{text-transform:uppercase}ul{-webkit-padding-start:0;padding-inline-start:0}ul li{-webkit-padding-start:0;padding-inline-start:0}h1,h2,h3,ul,dt{color:#66fcf1}h1,h2{margin:1rem;line-height:3rem}a{text-decoration:none;color:#45a29e}nav{background-color:#0b0c10;padding:20px 0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}nav ul{justify-content:space-around;list-style:none}nav ul li{margin:10px}.active--link{color:#66fcf1}.home{text-align:center;text-transform:uppercase;position:absolute;top:50%;left:50%;transform:translateX(-50%)}#tsparticles{height:calc(100vh - 80px)}header{background-image:url(/static/media/code-min.7c0f5d4b.JPG)}main{display:flex}main ul li{list-style-image:url(/static/media/ok.bd3ea695.svg);list-style-position:inside;margin:1rem}article{width:90vw;padding-bottom:70px;margin:30px}.photo{background-image:url(/static/media/viktoriia_aqua-min.0c20c31d.png);background-position:20% 0;background-repeat:no-repeat;width:500px;height:680px;display:none}.contacts-list{min-width:50vw}.contacts-list ul,.contacts-list li{list-style:none}.flex-container,nav ul,main{height:100%;display:flex;flex-direction:row;flex-wrap:wrap;align-content:center;align-items:center;justify-content:center}.align-start{align-items:flex-start}.flex-item{order:0;flex:0 0 auto;align-self:flex-start}.flex--all-width{flex:0 0 100%;text-align:center}.space-around{justify-content:space-around}.level{width:200px;display:inline-block;opacity:0.4;border:1px solid white}dd,dt{margin:5px 0px}p{margin:10px 0}span:last-child{padding-left:20px;text-align:end;flex:0 1 200px}.space-between,p{display:flex;justify-content:space-between}@media (max-width: 575.98px){h2{font-size:1.8rem}h1,h2{line-height:2rem}main{font-size:18px}main ul{margin:0px 5px}main ul li{margin:10px 0}.home{top:40%;font-size:0.7rem}article{margin:15px 15px}.level{width:30%}.only-for-small-width{font-size:0.8rem}.flex-container,nav ul,main{flex-direction:column}nav .flex-container,nav ul,nav main{flex-direction:row}}@media (min-width: 576px) and (max-width: 767.98px){.home{top:30%;font-size:1rem}}@media (min-width: 992px){.photo{display:block}article{width:calc(100vw - 600px)}}

